<template>
    <div class="container">
        <section class="row">
            <div class="col-xs-12">
                <v-breadnav>关于我们</v-breadnav>
            </div>
        </section>
        <section class="row ">
            <div class="col-xs-12"> 
                <div class="content about-us clearfix">
                    <ul class="nav-list pull-left">
                        <li v-for="(item,index) in aboutUsList" :key="index" :class="index == 0 ? 'active' :''">
                            <a :href="hrefArr[index]"  role="tab" data-toggle="tab">{{item.documentName}}</a>
                        </li>  
                    </ul>               
                    <div class="tab-content">
                        <div role="tabpanel"  v-for="(item,index) in aboutUsList" :key="index" :class="index == 0 ? 'tab-pane active' :'tab-pane'" :id="idArr[index]">
                            <div class="title"><h3>{{item.documentName}}</h3></div>
                            <div class="aboutus-item" v-html="item.documentUrl">
                            </div>
                        </div>
                    </div>
                </div>                     
            </div>
        </section>
    </div>
    
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            aboutUsList:[],
            hrefArr:['#about-us','#add-us','#services','#about-saleafter','#contact-us','#buycar-flow'],
            idArr:['about-us','add-us','services','about-saleafter','contact-us','buycar-flow']
        }
    },
    components:{
        vBreadnav
    },
    methods:{
        getListData:function(){
            let _this = this;
            let data = {};
            data.platForm = "020003";
             $.ajax({
                url:PC_COMMONURL+'/queryDocumentList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.aboutUsList = data.data.documentList;
                    }
                }
            })
        }
    },
    mounted:function(){
        this.getListData();
    }   
}
</script>



